body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-bar
{
    height: 100%;
    width: 70px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
}
.link
{
    display:block;
    margin-top: 150px;
    text-decoration: none;
}
.icon
{
 width: 45px;
 height: 45px;
 border: solid white 2px;
 border-radius: 5px;
}
.unselected
{
    border:none
}


.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
 .App{
   margin-left:80px;

 }
}
.flex-column
{
  flex-direction: column;
}
.flex-row
{
  flex-direction: row;
}

.homepage-title
{
  font-family: 'Arvo', serif;
  font-size: 50px;
  justify-self: center;
  padding-bottom: 100px;
}
.homepage
{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.login-link
{
  text-decoration: none;
  color: black;
}

.profile-container
{
    padding-left:80px;
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    float:left;
    border-right: 2px groove rgba(28,110,164,0.22);
    height: 100vh;
}
.Profile
{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.profile-picture
{
    background: #fff;
    border: 5px solid #fff;
    padding-top: 20px;
}

.picture
{
    width:15vh;
    height:15vh;
    border-radius: 50%;
}
.friends-title
{
    font-weight: bold;
    text-align: left;
    padding-left: 10px;
}
.bio
{
    font-size: 16px;
    color:grey;
    width:20vh;
    text-align: left;
    padding:10px 0 10px 10px;
}

.friends
{
    display:grid;
    flex-direction: column;
    flex-wrap:wrap;
    overflow: hidden;
}

.main
{
    overflow:auto;
    width:30vh;

    border-right: 2px groove rgba(28,110,164,0.22);
}

.profile-username
{
    font-size:30px;
    font-weight: bold;
    padding-left:10px;
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    display: inline-block;
}
.posts-title
{
    text-align: center;
    text-decoration: underline;
    padding-bottom: 50px;
}
.posts
{
    display: flex;
    flex-direction: column;
    padding-left:30px;
   
}

.statistics
{
 width:auto;
 display: flex;
 flex-direction: column;
}


.pie-chart
{
    width: 500px;
    height: 400px;
    padding-left:100px;
}
.tags
{
    display: flex;
    flex-direction: row;
}
.tag{
 padding-left:75px;
}
.block
{
    width:10px;
    height: 10px;
    display: inline-block;
    margin-right:105px;
}
.yellow
{
 background-color: #d7f226;
}
.red
{
 background-color:#c11b1b;
}
.green
{
    background-color: #0dc441;
}

.post-input
{
    resize:none;
