}

.friend-name
{
height: 40px;
}
.friend-link
{
    text-decoration: none;
    color:black;
}
.post-title
{
    font-size:20px;
    font-weight: 50;
    text-decoration: underline;
}
.post-content
{
    color:grey;
    font-size: 15px;
}
.post
{
    padding-top:10px;
    padding-right: 20px;
}
.user-stats
{
    width:300px;
    display:inline-block;
    cursor: pointer;
    background: #fefefe;
    padding: 10px 5px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: -webkit-transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
    margin:0 auto;
    z-index: 4;
}
.user-stats:hover
{ 
    -webkit-transform: scale(1.1); 
            transform: scale(1.1);
}
.username
{   
    display:inline-block;
}
.points
{
    color:green;
    font-size:15px;
    display: inline-block;
}
.image
{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #ebeef3;
    overflow: hidden;
    padding: 5px;
}

